body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

@keyframes move {
  0% {
    transform: rotate(-180deg);
  }

  100% {
    transform: rotate(180deg);
  }
}

.watch {
  box-sizing: border-box;
  border: 4px dotted #003;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__minutes {
  transform-origin: bottom;
  background-color: #9cf;
  border-radius: 50% 50% 25% 25%;
  width: 3%;
  height: 25%;
  animation: 60s linear infinite move;
  position: absolute;
  top: 25%;
}

.watch__seconds {
  transform-origin: bottom;
  background-color: #009;
  border-radius: 50% 50% 25% 25%;
  width: 1%;
  height: 50%;
  animation: 5s linear infinite move;
  position: absolute;
}

.watch__center {
  z-index: 1;
  background-color: #0ff;
  border-radius: 50%;
  width: 4%;
  height: 4%;
  position: absolute;
  top: 48%;
}

